
const BASE_URL = 'http://localhost:8080/api'
// const BASE_URL = 'http://192.168.1.11:8080/wine'
// const BASE_URL = 'https://www.homiesocial.cn/wine'
// exportmyRequest,myRequestoptions
export const myRequest = (options)=>{  
    return new Promise((resolve,reject)=>{  // promisereturn
        uni.request({  // 
            url: BASE_URL + options.url,  // options
            method: options.method || 'GET', // optionsmethodGET
            data: options.data || {},  // dataoptionsdata 
            success: (res) => {  // res
                if(res.data.code !== 0){  // 
                    return uni.showToast({
                        title: ''
                    })
                }
                resolve(res)  //resolve
            },
            fail: (err)=>{ // err 
                uni.showToast({  // 
                    title: ''
                })
                reject(err) // reject
            }
            
        })
    })
}

export const fileRequest = (options)=>{  
    return new Promise((resolve,reject)=>{  // promisereturn
        uni.uploadFile({  // 
            url: BASE_URL + options.url,  // options
            method: options.method || 'GET', // optionsmethodGET
			filePath: options.filePath,
			name: "file",
            data: options.data || {},  // dataoptionsdata 
            success: (res) => {  // res
				let data= JSON.parse(res.data); 
                if(data.code !== 0){  // 
                    return uni.showToast({
                        title: ''
                    })
                }
                resolve(data)  //resolve
            },
            fail: (err)=>{ // err 
                uni.showToast({  // 
                    title: ''
                })
                reject(err) // reject
            }
            
        })
    })
}